import { Component, EventEmitter, Input, Output } from '@angular/core';
import { DetailedExternalAccount } from '@backbase/data-ang/payment-order-a2a';
import { failedStatuses } from '../../helpers/constants';

@Component({
  selector: 'bb-a2a-account-details',
  templateUrl: 'account-details.component.html',
})
export class AccountDetailModalComponent {
  readonly modalOptions = {
    backdrop: 'static',
    keyboard: false,
  };
  statuses = failedStatuses;

  /**
   * Open account details modal flag
   * Default value is false
   */
  @Input() isOpen = false;

  /**
   * External account details
   */
  @Input() externalAccount?: DetailedExternalAccount;

  /**
   * Close account details modal event emitter
   */
  @Output() closeModal = new EventEmitter();

  onClose() {
    this.closeModal.emit();
  }
}
